body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 50px;
  margin: 0px 7px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* =================================
  Base Element Styles
==================================== */

* {
	box-sizing: border-box;
}
body {
  display: flex;
  flex-direction: column;
	font-family: 'Varela Round', sans-serif;
	line-height: 1.6;
	color: #3a3a3a;
	margin: 0;
  min-height: 100vh;
}
ul {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

h2 {
	color: #093a58;
	margin-top: 0;
}

h3 {
	color: #fff;
	margin-top: 0;
}

img {
	max-width: 100%;
}

/* =================================
  Base Layout Styles
==================================== */

/* ---- Navigation ---- */

.title {
	font-size: 1.35em;
  margin-left: 20px;
}
.main-title {
  display: flex;
}
.main-nav {
	display: flex;
	flex-direction: column;
	z-index: 3;
	width: 0;
	height: 100vh;
	position: fixed;
	top: 0;
	right:0;
	background-color: #778899;
}
.name a,
.main-nav a {
	text-align: center;
	display: block;
	padding: 10px 10px;
}
.main-nav a {
	font-size: .95em;
	color: #3acec2;
	text-transform: uppercase;
}
.main-nav a:hover {
	color: firebrick;
}

/* ---- Layout Containers ---- */

.main-header {
  display: flex;
  justify-content: space-between;
  background-color: lightgrey;
	padding-top: .5em;
	padding-bottom: .5em;
}
.banner,
.section-title,
.mainFooter {
	text-align: center;
}
.banner {
	color: #fff;
	background: #3acec2;
	padding: 3.2em 0;
	margin-bottom: 48px;
}
.box {
  text-align: center;
  background-color: grey;
  margin: 1em;
  min-height: 200px;
}
.mainFooter {
	background: #d9e4ea;
	padding: 1em 0;
  width: 200px;
  margin-top: 50px;
  margin: auto;
}
.menu {
  width: 35px;
  height: 35px;
  margin: auto;
  position: absolute;
	top: 20px;
	right: 20px;
}
.headline {
	margin-bottom: 0;
}
.closebtn {
	position: relative;
	right: -20px;
}
.content {
	display: flex;
	flex-direction: column;
}
.hide {
	display: none;
}

/* =================================
  Media Queries
==================================== */

@media (min-width: 501px) {
  .content {
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }
  .box {
    width: 40%;
    height: 20%;
  }
}
@media (min-width: 801px) {
  .main-nav {
		flex-direction: row;
		z-index: 3;
		width: auto;
		height: auto;
		position: relative;
		margin-top: 10px;
		margin-right: 30px;
		background-color: lightgrey;
		justify-content:flex-end;
  }
  .menu {
    display: none;
  }
	.close-icon{
		display: none;
	}

}
@media (min-width: 1201px) {
  .content {
    width: 1200px;
    margin: 0 auto;
  }
}

